import {Component, DoCheck, OnInit} from '@angular/core';
import {Location} from '@angular/common';
import 'rxjs/add/operator/filter';
import {UserInfo} from '../user/user-info';
import {RegisterService} from '../register/register.service';

declare var $: any;


@Component({
  selector: 'app-employee',
  templateUrl: './employee.component.html',
  styleUrls: ['./employee.component.css']
})
export class EmployeeComponent implements OnInit, DoCheck {

  public menus: Array<Menu>;
  public currUrl;
  public title = ['', ''];
  public currId: string;
  public currUser: UserInfo = new UserInfo('', '', '', '', '', '', '');

  constructor(private location: Location, private registerService: RegisterService, ) {
    this.menus = [
      new Menu('glyphicon glyphicon-home', '', ['./home'], []),
      new Menu('glyphicon glyphicon-user', '', ['./self-info'], []),
      new Menu('glyphicon glyphicon-book', '', ['./user-list'], []),
      new Menu('glyphicon glyphicon-list-alt', '', ['./work-list'], []),
      new Menu('glyphicon glyphicon-file', '', ['./form-apply'], [
        new Menu('', '', ['./form-apply/form-type', 'quit'], []),
        new Menu('', '', ['./form-apply/form-type', 'out'], []),
        new Menu('', '', ['./form-apply/form-type', 'holiday'], [])
      ])
    ];
  }

  ngOnInit() {
    this.registerService.returnUserInfo(function (user) {
      if (user) {
        this.currUser = user;
        this.currId = user.id;
      }
    }.bind(this));
    if ($.AdminLTE.controlSidebar) {
      $.AdminLTE.controlSidebar.activate();
    }
  }

  ngDoCheck(): void {
    if(this.currUrl!=this.location.path()){
      this.currUrl= this.location.path();
      this.title = this.setTitle(this.menus, this.currUrl);
    }
  }

  setTitle(menus: Array<Menu>, url) {
    let title=['',''];
    menus.forEach(menu => {
      if (url.match(menu.link)) {
        title = [menu.title, ''];
        if (menu.children.length > 0) {
          menu.children.forEach(menu => {
            if (url.match(menu.link[1])) {
              title[1] = menu.title;
              return;
            }
          })
        }
        return;
      }
    });
    return title;
  }
}

export class Menu {
  constructor(public icon: string,
              public title: string,
              public link: Array<string>,
              public children: Array<Menu>) {
  }
}



